.Mobile {
    align-items: flex-start;
    background-color: #000000a3;
    height: 100%;
    justify-content: center;
    left: 0;
    overflow-y: auto;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 20;
    &__navigation {
        border-radius: 8px;
        position: relative;
        overflow: hidden;
        z-index: 25;
        padding-top: 32px;
        background-color: #333333;
        &-list {
            margin: 32px 0;
            &-item {
                font-family: Open Sans;
                font-style: normal;
                font-weight: bold;
                font-size: 12px;
                line-height: 17px;
                text-transform: uppercase;
                padding: 8px;
                &-link {
                    text-decoration: none;
                    color: #fff;
                }
            }
        }
        &-close {
            display: none;
        }
        &-container {
            display: flex;
            justify-content: space-between;
            width: 280px;
            margin: 0 auto;
        }
    }
}

.Social {
    display: flex;
    margin: 16px 0;
    &__icon {
        margin: 8px;
        &-svg {
            width: 48px;
        }
    }
}

.Working {
    &__hours {
        font-family: Open Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 11px;
        line-height: 11px;
        color: #ffffff;
    }
}